.slider {
  position: relative;
  img {
    width: 100%;
    display: block;
  }
  &__wrapp {
    position: relative;
  }
  &__description {
    @include vertical_center_transform(absolute, 500px);
    font-size: 42px;
    color: #fff;
    padding: 21px 45px 20px 31px;
    left: 71px;
    background-color: rgba($brand-color, .8);
    z-index: 10000;
  }
  &__nav {
    color: #fff;
    background-color: rgba($brand-color, .8);
    height: 128px;
    @include vertical_center_transform(absolute, 37px);
    z-index: 1;
    text-align: center;
    cursor: pointer;
    .icon {
      @include vertical_center_transform(relative, 100%);
    }
    &_left {
      left: 0;
    }
    &_right {
      right: 0;
    }
    
    &:hover,
    &:active {
      background-color: #fff;
      color: rgba($brand-color, .8);
    }
  }
  .slick-dots {
    text-align: center;
    margin-top: 31px;
    li {
      display: inline-block;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background-color: $brand-color;
      opacity: .33;
      margin: 0 11px;
      cursor: pointer;
    }
    .slick-active {
      opacity: 1;
    }
    button {
      display: none;
    }
  }
}
@media screen and (max-width: 991px) {
  .slider {
    &__description {
      font-size: 23px;
      width: 287px;
      padding: 9px 17px 11px 23px;
    }
  }
}
@media screen and (max-width: 767px) {
  .slick-dots {
    display: none !important;
  }
  .slider {
    &__nav {
      width: 20px;
      height: 28px;
    }
    &__description {
      font-size: 15px;
      padding: 6px 20px 6px 7px;
      width: 191px;
    }
  }
}