
/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
.header {
  padding-top: 28px;
  padding-bottom: 14px;
  width:100%;
  // min-width: $min-width;
  text-align:left;
  -webkit-order: 1;
          order: 1;
  &__container {
    max-width: 1240px;
    margin: 0 auto;
    padding: 0 13px;
  }
  // LOGO
  &__logo {
    margin-top: 7px;
    float: left;
    .icon {
      font-size: 17.7em;
    }
    a {
      display: inline-block;
      font-size: 14px;
      color: $second-color;
      text-decoration: none;
      span {
        display: block;
        margin-top: 3px;
      }
    }
  }
  // SEARCH
  &__search {
    max-width: 496px;
    // width: 100%;
    margin-left: 290px;
    margin-right: 400px;
    // margin-left: 42px;
    position: relative;
    &__btn {
      color: $brand-color;
      position: absolute;
      right: 1px;
      top: 5px;
      font-size: 1.7em;
    }
    input {
      font-size: 14px;
      padding-left: 10px;
      height: 40px;
      width:100%;
      // min-width: $min-width;
      @include radius(4px);
      border: solid 1px $bodrer-input-color;
    }
  }
  // CONTACTS
  &__contacts {
    float: right;
    &__tel {
      padding-top: 11px;
      float: left;
      font-size: 23px;
      a {
        color: $text-color;
        letter-spacing: -.01em;
      }
    }
    &__info {
      margin-left: 17px;
      margin-top: 7px;
      float: right;
      font-size: 14px;
      li {
        letter-spacing: -.01em;
      }
      li:first-of-type {
        margin-bottom: 3px;
        color: $brand-color;
      }
    }
  }
}


@media screen and (max-width: 991px) {
  .header {
    padding-top: 22px;
    padding-bottom: 0;
    &__logo {
      margin-top: -3px;
      a {
         
         span {
          margin-top: -3px;
          font-size: 13px;
         }
       }
      .icon {
        font-size: 12.8em;
      }
    }
    &__search {
      margin-left: 220px;
      margin-right: 260px;
      &__btn {
        font-size: .9em;
      }
      input {
         font-size: 10px;
         height: 31px;
       }
    }
    &__contacts {
      &__tel {
        font-size: 15px;
        padding-top: 2px;
      }
      &__info {
        margin-top: -2px;
        font-size: 10px;
      }
    }
  }
}


@media screen and (max-width: 767px) {
  .header {
    padding-top: 53px;
    text-align: center;
    padding-bottom: 18px;
    &__logo {
      margin-top: 13px;
      margin-left: 6px;
      text-align: left;
      .icon {
        font-size: 13.8em;
      }
      a span {
        margin-top: 0;
      }
    }
    &__contacts,
    &__search {
      padding-top: 13px;
      background-color: $grey-bg;
      cursor: pointer;
      .icon {
        color: $icon-media;
        display: inline-block;
      }
      position: fixed;
      top: 0;
      width: 16.66%;
      height: 40px;
      z-index: 1;
      margin: 0;
    }
    &__contacts {
      right: 0;
      &__tel {
        padding: 0;
        .icon {
          font-size: 1.3em;
        }
        float: none;
      }
    }
    &__search {
      left: 33.32%;
      &__btn {
        font-size: 1.3em;
        position: static;
        padding: 0;
      }
    }
  }
}